import React from "react";
import Input from "../Common/Inputs";
import { FormBlock, ButtonWithMarginTop, Error } from "./Common";
import LoadingSpinner from "../Common/LoadingSpinner";

function Register({ loading, error, onChange, submit, form, goLogin }) {
  return (
    <FormBlock>
      <form onSubmit={submit}>
        <Input
          login
          autoComplete="userid"
          name="userid"
          type="text"
          onChange={onChange}
          value={form.userid}
          placeholder=""
        />
        <Input
          login
          autoComplete="password"
          name="password"
          placeholder=""
          onChange={onChange}
          value={form.password}
          type="password"
        />
        <Input
          login
          autoComplete="password"
          name="password_check"
          placeholder=" "
          onChange={onChange}
          value={form.password_check}
          type="password"
        />
        <Input
          login
          autoComplete="name"
          name="name"
          placeholder=""
          onChange={onChange}
          value={form.name}
          type="text"
        />

        {/* {error.type === "login" && <Error>{error.description}</Error>} */}

        <ButtonWithMarginTop login onClick={submit}>
          
        </ButtonWithMarginTop>
        <ButtonWithMarginTop login onClick={goLogin}>
          
        </ButtonWithMarginTop>
        {loading && loading.type === "login" && (
          <LoadingSpinner></LoadingSpinner>
        )}
        {error && error.type === "register" && (
          <Error>{error.description}</Error>
        )}
        {form.result && <Error>{form.result}</Error>}
      </form>
    </FormBlock>
  );
}

export default Register;
